        <div class="user-avatar-container">
          <el-avatar
            :size="200"
            src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png"
          ></el-avatar>
        </div>
        <div class="userItem">
          <h1>个人信息</h1>
        </div>
      </el-aside>
      <el-main class="page-main-contain">
        <el-tabs tab-position="top" style="height: 100%">
          <el-tab-pane label="我的收藏">
            <el-table :data="tableData">
              <el-table-column
                prop="type"
                align="center"
                label="类型"
                width="130px"
              >
              </el-table-column>
              <el-table-column
                prop="name"
                align="center"
                label="名称"
                width="200px"
              >
              </el-table-column>
              <el-table-column
                prop="author"
                align="center"
                label="作者"
                width="130px"
              >
              </el-table-column>
              <el-table-column
                prop="address"
                align="center"
                label="出版社"
                width="100px"
              >
              </el-table-column>
              <el-table-column fixed="right" align="center">
                <template slot-scope="scope">
                  <el-button
                    @click="takesBookItem(scope.row)"
                    :icon="istakes"
                    circle
                  ></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </el-main>
