<script>
export default {
  data() {
    const item = {
      type: "CS",
      name: "深入理解计算机系统",
      author: "xxx",
      address: "xxx",
    };
    return {
      istakes: "el-icon-star-on",
      tableData: Array(5).fill(item),
    };
  },
  methods: {
    goBack() {
      this.$router.push("/home");
    },
    takesBookItem(row) {
      if (this.istakes === "el-icon-star-on") {
        this.istakes = "el-icon-star-off";
      } else {
        this.istakes = "el-icon-star-on";
      }
    },
  },
};
</script>

